<template>
  <div class="context">
    <div class="content">
      <h3 class="title">Добавить аккаунт TalkyTime:</h3>
      <input id="login-input" class="swal2-input" placeholder="Введите логин">
      <input id="password-input" class="swal2-input" placeholder="Введите пароль">
      <button v-on:click="createUser" class="btn btn-primary btn-load btn-lg">Создать</button>
    </div>
  </div>
</template>

<script>
import "sweetalert2/dist/sweetalert2.css";
import Connection from "@/rsocket/connection";
import notification from "@/notify/notification";
import Swal from "sweetalert2";
import router from "@/views/router/routes";

export default {
  name: "CreateTalkyTime",
  methods: {
    createUser() {
      let login = ($('#login-input').val() || "").trim();
      if (login.length === 0) {
        notification('warning', 'Введите логин');
        return;
      }
      let password = ($('#password-input').val() || "").trim();
      if (password.length === 0) {
        notification('warning', 'Введите пароль');
        return;
      }
      Swal.fire({
        title: 'Вы уверены?',
        text: "Данное действие добавит новый аккаунт TalkyTime!",
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: 'Да! Я знаю, что делаю.',
        cancelButtonText: 'Нет! Я еще подумаю.'
      }).then((result) => {
        if (result.isConfirmed) {
          Connection.createTalkyTime(login, password, (res) => {
            notification("success", "Аккаунт TalkyTime успешно добавлен");
            router.push("/dashboard/talkytime/accounts");
          }, (error) => {
            notification('error', error.prettyText);
          });
        }
      });
    }
  },
  data() {
    return {
      user: [],
    }
  }
}
</script>

<style lang="scss" scoped>
.content {
  box-sizing: border-box;
  background: #222427;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  color: white;
  text-align: center;
}
</style>